import React from 'react';
import { Link } from 'react-router-dom';
import { ShowMenuButtonStyled } from './styles';

const ShowMenuButton = () => {
    return (
        <ShowMenuButtonStyled>
            <Link to="/detail">
            <p className="ButtonText"></p>
            </Link>
      </ShowMenuButtonStyled>
    );
};

export default ShowMenuButton;