import React, { FC, useCallback } from 'react';
import { AiOutlineClose } from 'react-icons/ai';
import { useHistory } from 'react-router';
import { Link } from 'react-router-dom';
import { ITotalMealInfo } from '../../types/ITotalMealInfo';
import { ITotalSnackInfo } from '../../types/ITotalSnackInfo';
import Button from '../Common/Button';
import DoughnutGraph from '../Common/DoughnutGraph';
import {
  MenuInfoContainer,
  MainImageContainer,
  MenuDetail,
  MenuNutrient,
  DoughnutContainer,
  Gap,
  MenuProtein,
  MenuCalorie,
  MenuIntegrity,
} from './styles';

interface MenuInfopRrops {
  currentMeal: ITotalMealInfo;
}

const MenuInfo: FC<MenuInfopRrops> = ({ currentMeal }) => {
  const history = useHistory();

  const goDetailPage = useCallback(() => {
    history.push('/detail');
  }, [history]);

  return (
    <MenuInfoContainer>
      <MainImageContainer>
        <img src={currentMeal.largeImgUrl} alt="Menu Img" />
        <AiOutlineClose onClick={goDetailPage} />
      </MainImageContainer>
      <MenuDetail>
        <h3>P </h3>
        <h2>{currentMeal.title}</h2>
        <MenuIntegrity>
          <MenuCalorie>
            <p>{currentMeal.cal}</p>
            <p></p>
          </MenuCalorie>
          <Gap />
          <MenuProtein>
            <img src={currentMeal.proteinImgUrl} alt="Integrity" />
            <p>{currentMeal.integrity} </p>
          </MenuProtein>
        </MenuIntegrity>
        <MenuNutrient>
          <DoughnutContainer>
            <DoughnutGraph ingredient="" amount={currentMeal.protein} />
          </DoughnutContainer>
          <DoughnutContainer>
            <DoughnutGraph ingredient="" amount={currentMeal.fat} />
          </DoughnutContainer>
          <DoughnutContainer>
            <DoughnutGraph ingredient="" amount={currentMeal.carb} />
          </DoughnutContainer>
        </MenuNutrient>
      </MenuDetail>
      <Button>
        <Link to="/subscribe-option"> </Link>
      </Button>
    </MenuInfoContainer>
  );
};

export default MenuInfo;
