import { IUser } from '../types/IUser';

//   
const LOGIN = 'auth/LOGIN' as const;
const LOGOUT = 'auth/LOGOUT' as const;
const CHECK = 'auth/CHECK' as const;

//   
// 
export const login = () => ({
  type: LOGIN,
});

export const logout = () => ({
  type: LOGOUT,
});

export const check = (data: IUser[]) => ({
  type: CHECK,
  data,
});

//  
type AuthAction =
  | ReturnType<typeof login>
  | ReturnType<typeof logout>
  | ReturnType<typeof check>;

//  
type AuthState = {
  user: IUser[];
};

//  
const initialStete: AuthState = {
  user: [],
};

//  
function auth(state: AuthState = initialStete, action: AuthAction): AuthState {
  switch (action.type) {
    case LOGIN:
      return {
        ...state,
        // email, password ..
      };
    case LOGOUT:
      return {
        ...state,
        user: [],
      };
    case CHECK:
      return {
        ...state,
        user: action.data,
      };
    default:
      return state;
  }
}

export default auth;
