import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { ReactComponent as LeftIcon } from '../assets/images/LeftIcon.svg';
import Button from '../components/Common/Button';
import Header from '../components/Common/Header';
import OrderInfos from '../components/Subscribe/OrderInfos';
import { useLocation } from 'react-router';

import {
  AddrssDetail,
  DelivertAddress,
  MesseageDetail,
  MesseageTitle,
  OrderInfo,
  SubscribeCompleteContainer,
  SubscribeCompleteMesseg,
} from '../components/Subscribe/styles';

import { useSelector } from 'react-redux';
import { RootState } from '../reducers';

const SubscribeCompletePage = () => {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  const { proteinInfo, dateInfo, subscribeTerm } = useSelector(
    ({ option }: RootState) => ({
      proteinInfo: option.proteinInfo,
      dateInfo: option.dateInfo,
      subscribeTerm: option.subscribeTerm,
    }),
  );

  const { mainAddress, detailAddress } = useSelector(
    ({ address }: RootState) => ({
      mainAddress: address.mainAddress,
      detailAddress: address.detailAddress,
    }),
  );

  return (
    <>
      <Header pathname="/subscribe">
        <LeftIcon />
        <h3> </h3>
        <p></p>
      </Header>
      <SubscribeCompleteContainer>
        <SubscribeCompleteMesseg>
          <h2>
            Enjoy Helmeal, <br />
            and Keep the TRACK
          </h2>
          <MesseageTitle> </MesseageTitle>
          <MesseageDetail> </MesseageDetail>
        </SubscribeCompleteMesseg>
        <OrderInfo>
          <h4></h4>
          <OrderInfos
            proteinInfo={proteinInfo}
            dateInfo={dateInfo}
            subscribeTerm={subscribeTerm}
          >
            <DelivertAddress></DelivertAddress>
            <AddrssDetail>{mainAddress}</AddrssDetail>
          </OrderInfos>
        </OrderInfo>
      </SubscribeCompleteContainer>
      <div>
        <Link to="/">
          <Button></Button>
        </Link>
      </div>
    </>
  );
};

export default SubscribeCompletePage;
